import React from "react";
import { Navigation } from "../../lib";
import Row from "muicss/lib/react/row";
import Modal from "react-modal";
import Loan from "./MyLoan";
import Select from "muicss/lib/react/select";
import Option from "muicss/lib/react/option";
import encodeQueryData from "../common/encodeQueryData";
import PageNavigation from "../common/PageNavigation";

const google = window.google;

export default class MyLoansPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      employeeId: "",
    };
  }

  navToComps(in_nyc, property_address, city_state_zip, property_type_id, e) {
    e.stopPropagation();
    sessionStorage.setItem("comps_inNYC", in_nyc);
    this.props.setPageAndScrollPos("/loans/my-loans", window.scrollY);
    this.props.submitCompsSearch(
      `${property_address} ${city_state_zip}`,
      property_type_id
    );
  }

  navToDealDetails(
    deal_id,
    in_nyc,
    violations,
    additional_properties,
    deal_status,
    page,
    e
  ) {
    e.stopPropagation();
    if (deal_id !== null || violations >= 0 || additional_properties > 0) {
      this.props.setPageAndScrollPos("/loans/my-loans", window.scrollY);
      let data = {
        deal_id,
        in_nyc,
        violations,
        additional_properties,
        deal_status,
      };

      if (window.location.search) {
        data.previous_page = window.location.pathname + window.location.search;
      }

      Navigation.go(`/my-loans-dd/${page}?${encodeQueryData(data)}`);
    } else {
      this.props.showNoDataToast(
        "There is no violations or deal details on this loan."
      );
    }
  }

  openStreetView(lat, long, property_address, city_state_zip) {
    var sv = new google.maps.StreetViewService();
    sv.getPanorama(
      {
        location: {
          lat: parseFloat(lat),
          lng: parseFloat(long),
        },
        radius: 50,
      },
      (data, status) => {
        if (status === "OK") {
          this.props.setPageAndScrollPos("/loans/my-loans", window.scrollY);
          Navigation.go(
            `/comp-sv?${encodeQueryData({
              latitude: lat,
              longitude: long,
              address: property_address + " " + city_state_zip,
            })}`
          );
        } else {
          this.props.showNoDataToast(
            "No street view available for this property."
          );
        }
      }
    );
  }

  changeEmployee(e) {
    this.setState(
      {
        employeeId: e.target.value,
      },
      function () {
        if (this.state.employeeId !== "Mine") {
          this.props.getMyLoansById(this.state.employeeId, this.props.search);
        } else {
          this.props.loadMyLoans();
        }
      }
    );
  }

  updateFilter = (e) => {
    let queryString = window.location.search || "";

    if (queryString.includes("filter=")) {
      let index = queryString.indexOf("filter=") - 1;
      queryString = queryString.slice(0, index);
    }

    if (e.target.value) {
      queryString += queryString === "" ? "?" : "&";
      queryString += `${encodeQueryData({ filter: e.target.value })}`;
    }
    Navigation.go(window.location.pathname + queryString);
  };

  render() {
    const myLoans = this.props.loans.map((item, index) => (
      <Loan
        key={index}
        item={item}
        contact={this.props.contact}
        noContact={this.props.noContact}
        loadingContact={this.props.loadingContact}
        passDeal={this.props.passDeal}
        getFullContactInfo={this.props.getFullContactInfo}
        navToDealDetails={this.navToDealDetails.bind(this)}
        openStreetView={this.openStreetView.bind(this)}
        setPageAndScrollPos={this.props.setPageAndScrollPos}
        removeBanker={this.props.removeBanker}
        navToComps={this.navToComps.bind(this)}
      />
    ));
    return (
      <div>
        {!this.props.loading ? (
          <Row>
            <Row className="my-loans-selects">
              {JSON.parse(localStorage.getItem("data")).bank_president &&
              this.props.loans ? (
                <div className="custom-select">
                  <Select
                    onChange={this.changeEmployee.bind(this)}
                    value={this.state.employeeId}
                  >
                    {this.props.employees &&
                      this.props.employees.map((item, index) => (
                        <Option key={index} value={item.id} label={item.name} />
                      ))}
                    {this.props.employeeMessage
                      ? this.props.employeeMessage
                      : ""}
                  </Select>
                </div>
              ) : (
                ""
              )}
              <div className="custom-select">
                <Select
                  onChange={this.updateFilter}
                  value={this.props.filter || ""}
                >
                  <Option label="All Deal Statuses" value={0} />
                  <Option label="Missing Quote" value={1} />
                  <Option label="Quoted" value={2} />
                  <Option label="Under Application" value={3} />
                  <Option label="Closed" value={4} />
                </Select>
              </div>
            </Row>
            {myLoans.length ? (
              <div>
                {myLoans}
                <PageNavigation
                  currentPage={this.props.currentPage}
                  totalPages={this.props.pages}
                />
              </div>
            ) : (
              <div className={this.props.noLoans ? "" : "hidden"}>
                <div>
                  <div className="grey-circle">
                    <i className="material-icons">warning</i>
                  </div>
                  <h2 className="no-results">{this.props.message}</h2>
                </div>
              </div>
            )}
          </Row>
        ) : (
          <Modal
            className="loading-modal"
            isOpen={true}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}
