import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as myLoansActions from "../../actions/loansActions";
import * as compsActions from "../../actions/compsActions";
import MyLoansPage from "./MyLoansPage";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";
import getParameterByName from "../common/getParameterByName";

class MyLoansContainer extends React.Component {
  state = {
    scrolled: false,
    filter: "",
    page: null,
  };

  componentDidMount() {
    const page = this.props.location.search ? getParameterByName("page") : null;
    const filter = this.props.location.search
      ? parseInt(getParameterByName("filter"), 10)
      : null;
    this.setState({ page, filter });
    if (!this.props.comps?.previousScrollPos?.[0]) {
      this.loadMyLoans(page, this.props.loans.search, filter);
      if (JSON.parse(localStorage.getItem("data")).bank_president) {
        this.props.actions.getBankPresidentEmployee();
      }
    } else {
      setTimeout(() => {
        window.scrollTo(
          0,
          this.props.comps.previousScrollPos[
            this.props.comps.previousScrollPos.length - 1
          ]
        );
      }, 100);

      setTimeout(() => {
        this.props.compsActions.resetScrollPosition();
      }, 1000);

      this.setState({
        scrolled: true,
      });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      this.props.location.search !== prevProps.location.search ||
      this.props.loans.search !== prevProps.loans.search
    ) {
      const page = this.props.location.search
        ? getParameterByName("page")
        : null;
      const filter = this.props.location.search
        ? parseInt(getParameterByName("filter"), 10)
        : null;
      this.setState({ page, filter });
      this.loadMyLoans(page, this.props.loans.search, filter);
    }
  }

  loadMyLoans(page, search, filter) {
    this.props.actions.loadMyLoans(
      this.props.loans.employeeId,
      search,
      filter,
      page || 1
    );
  }

  render() {
    let toast = null;
    if (this.props.loans.successToast) {
      toast = (
        <Expire closeToast={this.props.actions.closeLoanToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.loans.message}</p>
            <Button variant="flat" onClick={this.props.actions.closeLoanToast}>
              Done
            </Button>
          </div>
        </Expire>
      );
    }
    if (this.props.loans.noDataToast) {
      toast = (
        <Expire closeToast={this.props.actions.closeNoDataToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.loans.message}</p>
            <Button
              variant="flat"
              onClick={this.props.actions.closeNoDataToast}
            >
              Done
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div>
        {toast}
        <MyLoansPage
          pages={this.props.loans.pages}
          search={this.props.loans.search}
          contact={this.props.loans.contact}
          noContact={this.props.loans.noContact}
          loadingContact={this.props.loans.loadingContact}
          loans={this.props.loans.myLoans}
          noLoans={this.props.loans.noLoans}
          message={this.props.loans.message}
          loading={this.props.loans.loading}
          currentPage={this.state.page || 1}
          submitCompsSearch={this.props.compsActions.submitCompsSearch}
          getFullContactInfo={this.props.actions.getFullContactInfo}
          showNoDataToast={this.props.actions.showNoDataToast}
          passDeal={this.props.actions.passDeal}
          setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
          employees={this.props.loans.employees}
          employeeMessage={this.props.loans.employeeMessage}
          getMyLoansById={this.props.actions.getMyLoansById}
          loadMyLoans={this.props.actions.loadMyLoans}
          removeBanker={this.props.actions.removeBankerFromMyLoans}
          filter={this.state.filter}
        />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    loans: state.loans,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(myLoansActions, dispatch),
    compsActions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(MyLoansContainer);
