import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Button from "muicss/lib/react/button";
import Divider from "muicss/lib/react/divider";
import Form from "muicss/lib/react/form";
import Textarea from "muicss/lib/react/textarea";
import Modal from "react-modal";
import { findDOMNode } from "react-dom";
import { Navigation } from "../../lib";
import ClientModal from "../common/ClientModal";
import { formatTotalComps } from "../../lib";

export default class Loan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      brokerModalOpen: false,
      passModalOpen: false,
      quote_id: "",
      note: "",
      broker_name: "",
    };
  }

  quoteLoan(e) {
    e.stopPropagation();
    this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
    Navigation.go(
      `/quote-form/1/${this.props.item.quote_id}/${encodeURIComponent(
        window.location.pathname
      )}`
    );
  }

  passLoanDialog(e) {
    e.stopPropagation();
    this.setState({ passModalOpen: true, quote_id: this.props.item.quote_id });
  }

  closePassDialog(e) {
    e.preventDefault();
    this.setState({ passModalOpen: false, quote_id: "", note: "" });
  }

  submitPassDialog(e) {
    e.preventDefault();

    if (!this.state.note) {
      this.setState({ noteRequired: true });
      return;
    }
    this.props.passDeal(this.state.quote_id, this.state.note);
    this.setState({ passModalOpen: false, quote_id: "", note: "" });
  }

  setNote(e) {
    let input = findDOMNode(e.target);
    let index = input.selectionStart;
    this.setState(
      { note: e.target.value, noteRequired: !e.target.value },
      () => {
        input.setSelectionRange(index, index);
      }
    );
  }

  openBrokerModal(broker_id, broker_name, e) {
    e.stopPropagation();
    this.props.getFullContactInfo(broker_id);
    this.setState({
      brokerModalOpen: true,
      broker_name: broker_name,
    });
  }

  closeBrokerModal() {
    this.setState({
      brokerModalOpen: false,
    });
  }

  removeMeFromLoan(e) {
    e.stopPropagation();
    this.props.removeBanker(this.props.item.rb_id, this.props.item.claim_id);
  }

  render() {
    return (
      <div className="loan">
        <div
          className="panel mui-panel"
          key={this.props.item.deal_id}
          onClick={this.props.navToDealDetails.bind(
            this,
            this.props.item.deal_id,
            this.props.item.properties[0].in_nyc,
            this.props.item.total_violations,
            this.props.item.additional_properties,
            this.props.item.deal_status,
            "write-up"
          )}
        >
          <Row className="panel-heading">
            <Col md="11" sm="11" xs="11">
              <p className="panel-info-value property-title">
                {this.props.item.properties[0].property_address}
                {this.props.item.first_look ? (
                  <span className="tag primary-tag first-look-tag inline-block">
                    First Look
                  </span>
                ) : (
                  ""
                )}
              </p>
            </Col>
            <Col md="1" sm="1" xs="1">
              <img
                alt="street-view-square-loan"
                src="/images/icons/street-view-square.png"
                className="street-view-square-loans"
                onClick={(e) => {
                  e.stopPropagation();
                  this.props.openStreetView(
                    this.props.item.latitude,
                    this.props.item.longitude,
                    this.props.item.properties[0].property_address,
                    this.props.item.properties[0].city_state_zip
                  );
                }}
              />
            </Col>
          </Row>
          <Row>
            <Col md="3">
              <p className="small-title deal-info-title">Address</p>
              <p className="panel-info-value">
                {this.props.item.properties[0].city_state_zip}
              </p>
              <p className="panel-info-value">
                {this.props.item.source === "lt" ? (
                  <i
                    className="orange-text orange-text-hover pointer"
                    onClick={this.props.navToDealDetails.bind(
                      this,
                      this.props.item.deal_id,
                      this.props.item.properties[0].in_nyc,
                      this.props.item.total_violations,
                      this.props.item.additional_properties,
                      this.props.item.deal_status,
                      "property-details"
                    )}
                  >
                    {this.props.item.additional_properties === 0
                      ? ""
                      : this.props.item.additional_properties === 1
                      ? "1 other property"
                      : this.props.item.additional_properties +
                        " other properties"}
                  </i>
                ) : (
                  ""
                )}
              </p>
            </Col>
            <Col md="3">
              <p className="small-title deal-info-title">Property Type</p>
              <p className="panel-info-value">
                {this.props.item.properties[0].property_type
                  ? this.props.item.properties[0].property_type
                  : "--"}
              </p>
            </Col>
            <Col md="3">
              <p className="small-title deal-info-title">Deal Status</p>
              <p className="panel-info-value inline-block">
                {this.props.item.status ? this.props.item.status : "--"}
              </p>
              {this.props.item.status === "Missing Quote" ? (
                <div className="loan-inline-btns">
                  <Button
                    className="mui-btn mui-btn--raised loan-quote-button text-center"
                    onClick={(e) => this.quoteLoan(e)}
                  >
                    Quote
                  </Button>
                  <Button
                    className="mui-btn mui-btn--raised loan-pass-button text-center"
                    onClick={(e) => this.passLoanDialog(e)}
                  >
                    Pass
                  </Button>
                </div>
              ) : (
                ""
              )}
            </Col>
            <Col md="3">
              <div>
                <p className="small-title deal-info-title">
                  {this.props.item.deal_status === "1" ||
                  this.props.item.deal_status === "2"
                    ? "Expected Closing Date"
                    : this.props.item.deal_status === "5"
                    ? "Closed Date"
                    : ""}
                </p>
                <p className="panel-info-value">
                  {this.props.item.deal_status === "1"
                    ? "N/A"
                    : this.props.item.expected_closing_date
                    ? this.props.item.expected_closing_date
                    : "--"}
                </p>
              </div>
            </Col>
          </Row>
          <Row>
            <Col md="3">
              <p className="small-title deal-info-title">Loan Amount</p>
              <p className="panel-info-value">
                {this.props.item.loan_amount
                  ? this.props.item.loan_amount
                  : "--"}
              </p>
            </Col>
            <Col md="3">
              <p className="small-title deal-info-title">Units / Square Feet</p>
              <p className="panel-info-value">
                {this.props.item.total_units || "-"} /{" "}
                {this.props.item.total_area || "-"}
              </p>
            </Col>
            {this.props.item.source === "lt" ? (
              <Col md="3">
                <p className="small-title deal-info-title">Client Name</p>
                <p className="panel-info-value">
                  {this.props.item.client_name
                    ? this.props.item.client_name
                    : "--"}
                </p>
              </Col>
            ) : (
              ""
            )}
            <Col md="3">
              <p className="small-title deal-info-title">Brokers</p>
              <div className="loan-brokers">
                {this.props.item.brokers &&
                  this.props.item.brokers.map((item, index) => {
                    return (
                      <span key={index}>
                        <p
                          onClick={(e) =>
                            this.openBrokerModal(
                              item.member_contact_id,
                              item.broker_name,
                              e
                            )
                          }
                        >
                          {item.broker_name}
                          <i className="material-icons icon-right medium-text middle">
                            open_in_new
                          </i>
                        </p>
                      </span>
                    );
                  })}
              </div>
            </Col>
          </Row>
          <Row style={{ marginBottom: "10px" }} />
          <Row>
            <Divider style={{ marginTop: "20px" }} />
            {this.props.item.source === "lt" ? (
              <Row>
                <Col md="12" className="comps-row">
                  <div
                    onClick={this.props.navToComps.bind(
                      this,
                      this.props.item.properties[0].in_nyc,
                      this.props.item.properties[0].property_address,
                      this.props.item.properties[0].city_state_zip,
                      this.props.item.properties[0].property_type_id
                    )}
                  >
                    <div>
                      <div
                        className={
                          this.props.item.properties[0].in_nyc &&
                          this.props.item.total_violations !== null
                            ? "border-right"
                            : ""
                        }
                      >
                        <p className="violation-value comps">
                          {this.props.item.total_comps
                            ? formatTotalComps(this.props.item.total_comps)
                            : "0"}
                          <span>Comps</span>
                        </p>
                      </div>
                    </div>
                  </div>
                  <div>
                    <div
                      className={
                        this.props.item.properties[0].in_nyc &&
                        this.props.item.total_violations !== null
                          ? "comps-row-violations"
                          : "hidden"
                      }
                      onClick={this.props.navToDealDetails.bind(
                        this,
                        this.props.item.deal_id,
                        this.props.item.properties[0].in_nyc,
                        this.props.item.total_violations,
                        this.props.item.additional_properties,
                        this.props.item.deal_status,
                        "violations"
                      )}
                    >
                      <div>
                        <p className="violation-value">
                          {this.props.item.total_violations}
                          <span>Violations</span>
                        </p>
                      </div>
                      <div>
                        <p className="violation-value">
                          {this.props.item.total_complaints}
                          <span>Complaints</span>
                        </p>
                      </div>
                    </div>
                  </div>
                </Col>
              </Row>
            ) : (
              <div
                onClick={(e) => this.removeMeFromLoan(e)}
                className="remove-from-loan"
              >
                <p>Remove me from this loan</p>
              </div>
            )}
          </Row>
        </div>
        <Modal
          className="note-modal"
          isOpen={this.state.passModalOpen}
          ariaHideApp={false}
          contentLabel="Modal"
          onAfterOpen={function () {}}
          onRequestClose={this.closePassDialog.bind(this)}
          shouldCloseOnOverlayClick={true}
        >
          <Form className="modal-form">
            <p className="add-note-modal">Reason for passing </p>
            <Textarea
              rows={1}
              value={this.state.note ? this.state.note : ""}
              require
              onChange={this.setNote.bind(this)}
              className="add-note-textarea"
            />
            {this.state.noteRequired ? (
              <span className="error-block">Required</span>
            ) : (
              ""
            )}
            <div className="add-note-buttons">
              <Button variant="flat" onClick={this.closePassDialog.bind(this)}>
                CANCEL
              </Button>
              <Button variant="flat" onClick={this.submitPassDialog.bind(this)}>
                PASS
              </Button>
            </div>
          </Form>
        </Modal>
        <ClientModal
          isOpen={this.state.brokerModalOpen}
          onRequestClose={this.closeBrokerModal.bind(this)}
          onAfterOpen={function () {}}
          contact={this.props.contact}
          name={this.state.broker_name}
          noContact={this.props.noContact}
          loading={this.props.loadingContact}
        />
      </div>
    );
  }
}
